.footer {
    position: relative;
    bottom: 14px;
    background-image: url("../../../img/background/bckg_footer-mobile.png");
    background-repeat: no-repeat;
    background-size: 100%;
    height: 307px;
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;

    &__content {
        gap: 30px;
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
        gap: 30px;

        h2 {
            font-size: 20px;
            @include media(small) {
                font-size: 24px;
            }
        }
        p {
            font-size: 18px;
            @include media(small) {
                font-size: 20px;
            }
        }
    }

    &__content-reseau {
        img {
            max-height: 30px;
            margin-right: 20px;
            @include media(small) {
            max-height: 39px;
            }
        }
       
        
    }
}
